.background {
  background-color: #292D3E;
  display: flex;
  height: 100vh;
  width: 100vw;
  position: relative;
}

.actions {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 240px;
  height: calc(100% - 50px);
  align-items: stretch;
}

.buttons {
  display: flex;
  gap: 10px;
}

.resourceArea {
  position: absolute;
  margin:20px;
}

.actionsArea {
  margin: 15px;
}

.gameArea{
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  margin:15px;
  background: url('../assets/background.jpg');
  position: relative;
  background-size: contain;
  min-height: 800px;
  min-width:1071px;
    max-height: 800px;
  max-width:1071px;
}